.stock-upload-container {
  margin-left: 30px;
  margin-top: 5px;
  .field-container {
    margin: 15px 20px;
    input[type="file" i],
    .custom-dropdown {
      width: 100%;
      margin-top: 10px;
    }
    .button {
      padding: 10px 20px;
      width: 120px;
    }
  }
  .flex-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .field-container:last-child {
    margin-bottom: 0px;
  }
  font-size: 12px;
  padding: 15px;
  width: 450px;
}
